<template>
  <section class="nearly-store">
    <header class="nearly-store-title"><span>附近的商家</span></header>
    <div class="nearly-store-item" v-for="(item,key) in nearlyStore.list" @click="tostore(item)">
      <div class="nearly-store-item-img"><img :src="item.imageUrl"></div>
      <div class="nearly-store-item-detail">
        <h3 class="nearly-store-name">{{item.name}}<span class="store-distance">{{item.distance|formatKm}}</span></h3>
        <div class="nearly-store-summary">{{item.summary}}</div>
        <div class="nearly-store-sales">销量:<span>{{item.sellAmount}}</span></div>
      </div>
    </div>
    <p class="loadMore"><span @click="loadMore">{{loadText}}</span><mt-spinner type="fading-circle" v-if="isLoading"></mt-spinner></p>
  </section>
</template>

<script scoped>
  import {getNearStore} from '../service/getData'
  import BMap from 'BMap'
  export default {
    name: 'nearly',
    data () {
      return {
        nearlyStore: {},
        pageNum: 1,
        pageSize: 2,
        loadText: '加载更多',
        isLoading: false,
        isLoadAll: false,
        pages: 0
      }
    },
    created: function () {
      let _this = this
      let latitude, longitude
      let geolocation = new BMap.Geolocation()
      getNearStore(this.pageNum,this.pageSize,localStorage.latitude, localStorage.longitude)
      .then(function (response) {
        _this.nearlyStore = response.data
        _this.pages = response.data.pages
      })
      .catch(function () {
        _this.$toast('加载附近商家数据失败!')
      })
    },
    methods: {
      tostore: function (val) {
        this.$router.push({
          name: 'store',
          query: {
            storeId:val.id,
            latitude: val.latitude,
            longitude: val.longitude
            
          },
          params: {
            prevName: 'home'
          }
        })
      },
      loadMore: function () { 
        let _this = this
        if (this.pages <= this.pageNum) {
          return false
        }
        this.loadText = '加载中'
        this.isLoading = true;
        if (this.pages > this.pageNum) {
          setTimeout(() => {
            getNearStore(++this.pageNum,this.pageSize,localStorage.latitude, localStorage.longitude)
            .then(res => {
              for (var i =0;i<res.data.list.length;i++){
                _this.nearlyStore.list.splice(_this.nearlyStore.list.length,1,res.data.list[i])
              }
              console.log(_this.nearlyStore.list)
              this.isLoading = false;
              if (this.pages <= this.pageNum) {
                this.loadText = '没有更多了'
              } else {
                this.loadText = '加载更多'
              }
            })
            .catch(function () {
              _this.$toast('加载附近商家数据失败!')
            })
          },500)
        }
      }
    },
    filters: {
      formatKm: function (val) {
        return val.replace('千米','km')
      }
    }
  }
</script>

<style>
  /* 附近的商家*/
  
  .nearly-store {
    /*padding-bottom: .3rem;*/
  }
  
  .nearly-store-title {
    height: .3rem;
    line-height: .3rem;
    text-align: center;
  }
   .nearly-store-title>span{
     position: relative;
   }
 .nearly-store-title>span:before{
    position: absolute;
    content: '';
    top: 50%;
    left: -.05rem;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    height: .02rem;
    width: .2rem;
    background-color: #E9E9E9;
 } 
 .nearly-store-title>span:after{
    position: absolute;
    content: '';
    top: 50%;
    right: -.05rem;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    height: .02rem;
    width: .2rem;
    background-color: #E9E9E9;
 } 
  .nearly-store-item {
    height: 1.2rem;
    padding-left: .15rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
  }
  
  .nearly-store-item-img {
    flex-shrink: 0;
    width: .9rem;
    padding: .15rem 0;
    border-bottom: .01rem solid lightgray;
  }
  
  .nearly-store-item-img img {
    height: .9rem;
    width: .9rem;
  }
  
  .nearly-store-item-detail {
    flex: auto;
    padding: .15rem .1rem;
    border-bottom: .01rem solid lightgray;
  }
  
  .nearly-store-name {
    color: black;
  }
  .nearly-store-name span{
    font-size:.13rem ;
  }
  .nearly-store-name .store-distance {
    float: right;
    color: #999999;
    font-weight: normal;
  }
  
  .nearly-store-summary {
    width: 100%;
    font-size: .12rem;
    margin: .03rem 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  
  .nearly-store-sales {
    font-size: .12rem;
  }
  
  .nearly-store-sales span {
    color: #FF8D21;
    margin-left: .05rem;
  }
  
  .nearly-store-name {
    height: .24rem;
  }
</style>


