<template>
<section class="hi-life-index">
  <header>
    <div class="seat" @click="toseat">{{city}}</div>
    <div class="search-bar">
      <div class="search-bar-tool"><input type="text" placeholder="搜索" @click.prevent="toSearch"></div>
    </div>
    <div class="notice"><i class="notice-img" @click="toNotice"></i></div>
  </header>
  <section class="hi-life-index-content">
    <swiper-view :swiperUrl="swiper2"></swiper-view>
    <fenlei-view></fenlei-view>
    <guanggao-view></guanggao-view>
    <hr/>
    <nearly-view></nearly-view>
  </section>
</section>
</template>

<script scoped>
import {getSwiper,getAllCity} from '../../service/getData'
import swiperView from '@/components/swiper'
import fenlei from '@/components/fenlei'
import guanggao from '@/components/common/guanggao'
import nearly from '@/components/nearly'
import BMap from 'BMap'  // 百度地图
export default {
  data () {
    return {
      nearlyStore: null,
      swiper2: '1',
      city: '请选择'
    }
  },
  methods: {
    toSearch: function () {
      this.$router.push({
        name: 'search'
      }) // 跳转到搜索页面
    },
    toNotice: function () {
      this.$router.push({
        name: 'notice'
      }) // 跳转到通知页
    },
    toseat: function () {
      this.$router.push({
        name: 'seat',
        params: {
          city: this.city
        }
      })
    }
  },
  created: function () {
    var _this = this
    getSwiper().then(res => {
      _this.nearlyStore = res.data
    })
    .catch(function () {
      _this.$toast('加载附近商家数据失败!')
    })
  },
  components: {
    'swiper-view': swiperView,
    'fenlei-view': fenlei,
    'guanggao-view': guanggao,
    'nearly-view': nearly
  },
  mounted: function () {
    // 1.获取到用户的当前位置（经纬度）
    let _this = this
    let geolocation = new BMap.Geolocation()
    let myCity = new BMap.LocalCity()
    geolocation.getCurrentPosition(function (r) {
      if (this.getStatus() === 0) {
        localStorage.setItem('latitude',r.point.lat)
        localStorage.setItem('longitude',r.point.lng)
        console.log('您的位置：' + r.point.lng + ',' + r.point.lat)
      } else {
        console.log('failed' + this.getStatus())
      }
    }, {enableHighAccuracy: true})
    // 2.获取到用户的当前城市
    if(localStorage.currCityName == '' || localStorage.currCityName == undefined){
       myCity.get(function (rs) {
        _this.city = rs.name
        localStorage.setItem('currCityName',rs.name)
        console.log('您所在的城市为:' + rs.name)
        /*getAllCity(currCityName)
        .then(res => {
          let key = 0
          for (key in res.data) {
            if (res.data[key].length > 0) {
              localStorage.setItem('cityCode',res.data[key].code)
            }
          }
        })*/
      })
    }else{
      //是否从选择城市页面跳转过来的
      if (_this.$route.query.city) {
        _this.city = _this.$route.query.city
      } else {
        _this.city = localStorage.currCityName;
      }
    }
    // 关于状态码
    // BMAP_STATUS_SUCCESS 检索成功。对应数值“0”。
    // BMAP_STATUS_CITY_LIST 城市列表。对应数值“1”。
    // BMAP_STATUS_UNKNOWN_LOCATION  位置结果未知。对应数值“2”。
    // BMAP_STATUS_UNKNOWN_ROUTE 导航结果未知。对应数值“3”。
    // BMAP_STATUS_INVALID_KEY 非法密钥。对应数值“4”。
    // BMAP_STATUS_INVALID_REQUEST 非法请求。对应数值“5”。
    // BMAP_STATUS_PERMISSION_DENIED 没有权限。对应数值“6”。(自 1.1 新增)
    // BMAP_STATUS_SERVICE_UNAVAILABLE 服务不可用。对应数值“7”。(自 1.1 新增)
    // BMAP_STATUS_TIMEOUT 超时。对应数值“8”。(自 1.1 新增)
  }
}
</script>

<style>
.hi-life-index,
.header-seach-comm {
  height: 100%;
  padding-top: .4rem;
  position: relative;
}
.hi-life-index>header,
.header-seach-comm>header {
  position: fixed;
  display: -webkit-box;
  /* 老版本 - 适配iOS 6-, Safari 3.1-6 */
  display: -webkit-flex;
  /* 新版本 - 适配Chrome */
  display: flex;
  background-color: #FF8D21;
  background: -webkit-linear-gradient(left, rgb(255, 161, 38), rgb(255, 139, 32));
  background: linear-gradient(to right, rgb(255, 161, 38), rgb(255, 139, 32));
  width: 100%;
  top: 0;
  height: .4rem;
}
.seat {
  color: white;
  width: .7rem;
  line-height: .4rem;
  text-align: right;
  padding-right: .24rem;
  position: relative;
}
.seat:after {
  content: '';
  width: .16rem;
  height: .16rem;
  right: .05rem;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  background: url("/static/image/home_address_open@3x.png") no-repeat no-repeat;
  background-size: 100% 100%;
}
/* 搜索*/

.search-bar {
  flex: auto;
  height: 100%;
  padding: .05rem 0;
  box-sizing: border-box;
}

.search-bar-tool {
  height: 100%;
  width: 100%;
  position: relative;
  padding-left: .3rem;
  border-radius: .3rem;
  background-color: #FFAD59;
}

.search-bar-tool:before {
  content: '';
  left: .07rem;
  top: 50%;
  width: .18rem;
  height: .18rem;
  position: absolute;
  background: url("/static/image/home_search@3x.png") no-repeat no-repeat;
  background-size: 100% 100%;
  transform: translateY(-50%);
}

.search-bar-tool input {
  height: 100%;
  width: 90%;
  vertical-align: top;
}

.search-bar-tool input::-webkit-input-placeholder {
  color: #FFFFFF;
}

.notice,
.share {
  width: .5rem;
  padding: .05rem 0;
  text-align: center;
}

.notice-img,
.share-img,
.back-img {
  display: inline-block;
  height: .3rem;
  width: .3rem;
}

.notice-img {
  background: url("/static/image/home_notice@3x.png") no-repeat no-repeat;
  background-size: 100% 100%;
}
/* 首页内容区*/

.hi-life-index-content {
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
</style>


