<template>
  <div class="fenlei">
    <div class="fenlei-item" v-for="(item,key) in fenleiJson" @click="fenleiDetail(item.id)"><img :src="item.icon">
      <p>{{item.name}}</p>
    </div>
  </div>
</template>

<script>
  import {getFenLei} from '../service/getData'
  export default {
    name: 'fenlei',
    data () {
      return {
        fenleiJson: null
      }
    },
    created: function () {
      var _this = this
      getFenLei()
        .then(function (response) {
          _this.fenleiJson = response.data
        })
        .catch(function () {
          _this.$toast('加载数据失败!')
        })
    },
    methods: {
      fenleiDetail: function (val) {
        this.$router.push({
          name: 'classify',
          query: {
            'classifyId': val
          }
        })
      }
    }
  }
</script>

<style scoped>
/* 分类模块*/

.fenlei {
  height: 1.86rem;
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

.fenlei-item {
  width: 20%;
  color: #333333;
  text-align: center;
}

.fenlei-item img {
  height: .45rem;
  width: .45rem;
}
</style>


