<template>
  <div class="swiper-view">
    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item,key) in swiperJson">
        <!--<router-link to="/swiper"></router-link>-->
        <img :src="item.imageUrl">
      </mt-swipe-item>
    </mt-swipe>
  </div>
</template>

<script>
  import {getSwiper} from '../service/getData'
  export default {
    name: 'swiperView',
    props: ['swiperUrl'],
    data () {
      return {
        swiperJson: null,
        axjxUrl: this.swiperUrl
      }
    },
    created: function () {
      var _this = this
      getSwiper()
        .then(function (response) {
          _this.swiperJson = response.data
        })
        .catch(function () {
          _this.$toast('请求失败!')
        })
    }
  }
</script>

<style scoped>
/* 轮播图*/
.swiper-view {
  height: 1.4rem;
}
.mint-swipe-item img {
  height: 100%;
  width: 100%;
}
</style>


