<template>
  <section class="advertisement">
    <div class="advertisement-item" v-for="(item,key) in guanggaoJson">
      <img :src="item.imageUrl">
    </div>
  </section>
</template>

<script>
  import {getGuanggao} from '../../service/getData'
  export default {
    name: 'guanggao',
    data () {
      return {
        guanggaoJson: null
      }
    },
    created: function () {
      var _this = this
      getGuanggao()
        .then(function (response) {
          _this.guanggaoJson = response.data
        })
        .catch(function () {
          _this.$toast('加载数据失败!')
        })
    }
  }
</script>

<style scoped>
  /* 首页广告模块 */
  
  .advertisement {
    padding: .15rem .1rem;
    height: 1.1rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
  }
  
  .advertisement-item {
    flex: 1;
    height: 100%;
    width: 100%;
    padding: 0 .06rem;
  }
  
  .advertisement-item:first-child {
    padding-right: 0;
  }
  
  .advertisement-item:last-child {
    padding-left: 0;
  }
</style>


